
$backgroundColor: var(--background-color);
$bodyColor: var(--light-text-color);
$bodyFont: -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";



$blockOne: 8px;
$blockTwo: 16px;
$blockThree: 32px;
$blockFour: 64px;
$blockFive: 128px;
$blockSix: 256px;
$blockSeven: 512px;


body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;
  transition: background-color 0.3s ease-in-out;
}

html, body {
  margin:0;
  padding:0;

}


h1 {
  padding: 0;
  margin: 0;
  font-size: 3.5rem;


}

h2 {
  padding: 0;
  margin: 0;
  font-size: 2rem;

  text-transform: uppercase;

}


h4 {

  padding: 0;
  margin: 0;
  margin-top: 0px;
  font-size: .8rem;
  line-height: .8rem;
  font-weight: normal;
  text-transform: uppercase;
}




html,
html[data-theme="light"] {
  --background-color: #efedea;
  --light-text-color: #151515;
  --link-color: rgb(0, 112, 201);
  --main-background-color: rgb(255, 255, 255);
  --main-text-color: rgb(51, 51, 51);
}

html[data-theme="dark"] {
  --background-color: #151515;
  --light-text-color: #efedea;
  --link-color: rgb(100, 210, 255);
  --main-background-color: rgb(28, 28, 30);
  --main-text-color: rgb(229, 229, 234);
}

@media (prefers-color-scheme: dark) {
  html,
  html[data-theme="dark"] {
    --background-color: #151515;
    --light-text-color: #efedea;
    --link-color: rgb(100, 210, 255);
    --main-background-color: rgb(28, 28, 30);
    --main-text-color: rgb(229, 229, 234);
  }

  html[data-theme="light"] {
    --background-color: #efedea;
    --light-text-color: #151515;
    --link-color: rgb(0, 112, 201);
    --main-background-color: rgb(255, 255, 255);
    --main-text-color: rgb(51, 51, 51);
  }
}



